"use client";

import React, { useEffect } from "react";
import ExamPage from "../module/[id]/exam/page";


interface ExamModalProps {
  isOpen: boolean;
  onClose: () => void;
  moduleId: number;
}

const ExamModal: React.FC<ExamModalProps> = ({ isOpen, onClose, moduleId }) => {
  // Evitar el scroll en el body cuando el modal est√° abierto
  useEffect(() => {
    if (isOpen) {
      document.body.style.overflow = "hidden";
    } else {
      document.body.style.overflow = "auto";
    }
  }, [isOpen]);

  if (!isOpen) return null;

  return (
    <div className="fixed inset-0 bg-black bg-opacity-80 flex justify-center items-center z-50">
      <div className="bg-white w-full h-full overflow-y-auto p-8 relative">
        {/* Bot√≥n para cerrar */}
        <button
          onClick={onClose}
          className="absolute top-4 right-4 bg-red-500 text-white p-2 rounded-lg"
        >
          ‚úñ Cerrar
        </button>

        {/* Contenido del examen */}
        <ExamPage moduleId={moduleId} />
      </div>
    </div>
  );
};

export default ExamModal;
