"use client"; 
import React from "react";

interface Resource {
  id: number;
  name: string;
  description?: string;  // üîπ Hacer `description` opcional
}

const ResourcesList = ({
  resources,
  showAllLink,
}: {
  resources: Resource[];
  showAllLink: string;
}) => {
  return (
    <div className="p-4 border rounded-lg shadow-md bg-white">
      <div className="flex items-center justify-between mb-4">
        <h3 className="font-semibold text-lg text-neutral-700 flex items-center gap-2">
          <img src="/assets/icons/resources-icon.svg" alt="Recursos" />
          Recursos
        </h3>
        <a
          href={showAllLink}
          className="text-primary hover:underline text-sm font-medium"
        >
          Ver todo
        </a>
      </div>
      <ul className="space-y-4">
        {resources.map((resource) => (
          <li key={resource.id} className="flex items-center gap-2">
            <div className="w-10 h-10 bg-gray-200 rounded"></div>
            <div>
              <p className="text-sm font-bold text-neutral-700">{resource.name}</p>
              <p className="text-xs text-neutral-500">{resource.description || "Sin descripci√≥n"}</p>
            </div>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default ResourcesList;
